        <h1 class="title"><b>杨铮团队国家自然科学基金项目结题成果科普性介绍</b></h1>
        <p class="title" style="font-size: 14px;text-indent: 0cm;">发布时间：2022-12-14</p>
        <p class="title">国家自然科学基金项目结题成果科普性介绍-<b>室内位置隐私保护关键密码技术研究</b></p>
        <p style="margin-bottom:10px;">本项目来源于国家自然科学基金项目“室内位置隐私保护关键密码技术研究(61872051)”,起止时间
            2019.01-2022.12。 本项目工作概括如下。</p>
        <h1>一、主要研究内容</h1>
        <p>本项目以研究室内位置隐私相关的关键密码理论与技术，并根据实际生活中的典型应用为背景研究位置服务基隐私保护协议构建技术，主要包括以下研究内容：</p>
        <p><b>研究内容1：</b>室内定位隐私安全模型。为了实现基于Wifi信号的室内定位隐私保护协议的可证明安全性，规避部署该类系统可能埋下安全隐患，本项目首先基于
            半诚实研究威胁模型研究室内定位隐私安全形式化模型，模拟了针对用户和服务器的攻击模型以模拟各种安全威胁和攻击，并将其用于证明了项目负责人在
            INFOCOM 2018上提出的基于混淆电路的室内隐私协议的安全性。此外，项目组基于恶意攻击者模型，在诚实用户为大多数情况下，研究了隐私位置证明协
            议的安全建模方式，分别形式化定义了匿名性，位置隐私性和可追踪性安全属性。</p>
        <p><b>研究内容2：</b>高效的室内定位服务隐私保护协议研究。本项目基于带有可信第三方的客户服务器模式，开展高效的室内定位服务隐私保护协议设计研究。
            针对基于传统的通用多方安全计算协议的协议方案存在效率低下等问题，项目负责人与合作团队共同开展可外包的室内定位服务隐私保护协议研究。为解决客户服务
            器定位模式下的隐私定位服务存在协议执行效率低下的问题，本项目基于ABY隐私保护框架研究了将部分协议计算过程外包给第三方高性能服务器的隐私协议构建方式。</p>
        <p><b>研究内容3：</b>室内位置隐私安全应用协议。 本项目针对位置基服务（如室内购物导航、寻找附近朋友并协商约会地点）的核心需要，重点研究高效位置
            接近性检测隐私协议。研究团队根据现有位置接近性检测隐私协议存在的效率问题，重点研究了室内环境下新型位置接近性检测隐私协议构建方法和关键密码技术，
            以提高现有协议的各方面性能。同时，本项目结合特定位置基应用（如共用位置区域协商协议等）开展隐私保护密码技术研究。</p>
        <p><b>研究内容4：</b>室内隐私位置服务异常行为保护技术。为了实现某些敏感室内环境下（如政府大楼内部）更高级别的隐私保护，研究团队开展了恶意威胁模型
            下的隐私保护。本项目重点从轻量级隐私保护技术为突破口，研究适用于资源受限客户端的隐私保护密码原语——包括新身份认证因子、消息认证、以及安全通信等。
            此外，本项目研究了轻量级位置证明协议构建技术，用以防止恶意客户端的位置信息滥用。</p>
        <p><b>研究内容5：</b>研制位置隐私服务安全原型系统。研究团队针对上述研究中提出的隐私保护算法和协议开展原型系统研制，研究室内位置隐私保护协议的优化
            方案、策略和算法，以提高协议在资源受限设备上（如移动端设备）执行、通信和存储性能。同时，基于研制的原型系统，开展了相关仿真实验，验证设计算法和协议的实用性。</p>
        <h1>二、主要研究进展和重要结果</h1>
        <p>本项目在所有研究目标均取得较好的研究进展，部分研究结果取具有一定的突破性。以下将结合上述研究内容和已发表或已录用的学术论文，对目前取得的主要研究进展和重要结果进行介绍：</p>
        <p class="min_title"><b>1）基于Wifi指纹的室内定位隐私安全模型研究</b></p>
        <p>在研究论文成果<sup>[1,2]</sup>中，项目组提出了一个用于分析基于 Wifi 指纹的室内定位隐私协议（Privacy-preserving Indoor Localization, PPIL）的安全模型。
            通过研究，我们发现现有许多现有的室内定位隐私安全模型的敌手模型较弱，并不能模拟实际生活中的许多典型攻击，例如已知位置攻击等。为涵盖各种最先进的实
            用型攻击，项目组提出了第一个形式化的室内定位安全模型，详细定义了协议实体客户端和服务器端的隐私保护安全目标。该模型尤其模拟了常见的恶意攻击行为，
            例如临时私钥泄露，恶意Wifi指纹查询，以及目标攻击区域选择攻击等。对于客户端隐私，该模型要求协议能防止敌手难以区分客户端的真实位置和随机位置；
            服务器端隐私要求敌手不能成功生成一个伪造的数据库来提供与服务器的真实数据库类似的功能，并结合3维欧几里得距离通过一种球形建模方式来刻画真实数据库
            和伪造数据库直接的功能区别。</p>
        <p style="text-indent: 0cm;"><b>前备知识。</b>我们首先描述了将在以下安全概念(特别是对于安全性实验)中使用的通用模拟环境。考虑两种类型的实体：客户端 <img
                src="http://latex.codecogs.com/gif.latex?\mathsf{C}"/>
            和服务器<img src="http://latex.codecogs.com/gif.latex?\mathsf{S}" />。服务器<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{S}" />根据客户的要求提供建筑物的室内定位服务(ILS)。
            建筑物区域(由位置服务覆盖)被巧妙地划分为<img src="http://latex.codecogs.com/gif.latex?M\" />个参考位置<img
                src="http://latex.codecogs.com/gif.latex?LT = \{ i,({x_i},{y_i},{z_i})\} _{i = 1}^M" />，
            如图1中的黑点所示，其中<img src="http://latex.codecogs.com/gif.latex?\left( {x_i},{y_i} \right)" />表示水平坐标，<img
                src="http://latex.codecogs.com/gif.latex?{z_i}" />
            表示垂直坐标(例如，楼层的位置)。此外，该楼宇部署了<img
                src="http://latex.codecogs.com/gif.latex?N\" />个Wifi接入点（AP）来提供网络服务，其中每个（i∈[N]）接入点可能具有唯一的身份
            <img src="http://latex.codecogs.com/gif.latex?A{P_i}" />。 <img
                src="http://latex.codecogs.com/gif.latex?APT = \{ A{P_j}\} _{j = 1}^N"/>是存储所有Wifi接入点的列表。具体来说，
            每个位置都有一个所谓的Wifi指纹，它由特定Wifi AP的接收信号强度值（RSS）组成，其中每个RSS值来自一个范围<img
                src="http://latex.codecogs.com/gif.latex?{R_v} = [{v_{\min }},{v_{\max }}]" />，<img src="http://latex.codecogs.com/gif.latex?({v_{\min }},{v_{\max }})" />分别是最小值和最大值。因此，假设服务器持有一个预先测量的Wifi指纹数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />，
            该数据库由一组元组<img
                src="http://latex.codecogs.com/gif.latex?< i,{V_i} = \{ {v_{i,j}}\} _{j = 1}^N > _{j = 1}^M" />组成，其中<img
                src="http://latex.codecogs.com/gif.latex?i\" />是参考位置
            <img src="http://latex.codecogs.com/gif.latex?{L_i} \in LT\" />的索引，<img
                src="http://latex.codecogs.com/gif.latex?{V_{i,j}}" />表示从<img
                src="http://latex.codecogs.com/gif.latex?A{P_j}" />
            的<img src="http://latex.codecogs.com/gif.latex?{L_i}" />处获得的RSS值。进一步，令Dist为距离函数，将两个位置<img
                src="http://latex.codecogs.com/gif.latex?{L_i}" />和<img
                src="http://latex.codecogs.com/gif.latex?{L_j}" />
