                src="http://latex.codecogs.com/gif.latex?({x_j},{y_j},{z_j})" />作为输入)，然后输出它们之间的距离。
            当 <img src="http://latex.codecogs.com/gif.latex?\mathsf{C}" /> 想要知道它的位置时，它首先测量所有APs的RSS值，得到实时Wifi指纹<img
                src="http://latex.codecogs.com/gif.latex?F = \{ {f_i}\} _{j = 1}^N" />。
