            位置查询F和对应的位置L。服务器的秘密是数据库<img src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />。
        </p>
        <p>为了模拟一组实体的行为（包括λ个客户端和1个服务器），我们可能要实现一个在<img
