        <P style="text-indent: 0cm;"><img src="http://latex.codecogs.com/gif.latex?\mathbf{-LocTest}\left( {\tau ,s} \right)" />：如果预言机的状态为<img
                src="http://latex.codecogs.com/gif.latex?ds_\tau ^s \ne accept" />或<img
                src="http://latex.codecogs.com/gif.latex?\tau  = \lambda  + 1" />，则该查询返回失败符号 <img
                src="http://latex.codecogs.com/gif.latex?\bot" />。否则，它执行以下步骤：（i）随机选择比特
            <img src="http://latex.codecogs.com/gif.latex?b \xleftarrow{\$} \left\{ {0,1} \right\}" />；（ii）选取一个随机索引<img
                src="http://latex.codecogs.com/gif.latex?j \in ins_\tau ^s" />，得到<img
                src="http://latex.codecogs.com/gif.latex?{F_j}: = \mathrm{FPTSim}\left( j \right)" />，并且根据 <img
                src="http://latex.codecogs.com/gif.latex?{F_j}" />和
            <img src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />计算<img
                src="http://latex.codecogs.com/gif.latex?{L_0}" />，使得<img
                src="http://latex.codecogs.com/gif.latex?{L_0} \ne L_\tau ^s" />；（iii）设置<img
                src="http://latex.codecogs.com/gif.latex?{L_1}: = L_\tau ^s" />（真实位置）。最终，返回位置<img
                src="http://latex.codecogs.com/gif.latex?{L_b}" />给敌手。
            在接下来的相应安全实验中，该查询最多允许被询问一次。我们将在此查询中选择的预言机<img
                src="http://latex.codecogs.com/gif.latex?\pi _\tau ^s" />称为<i>测试预言机</i>。
        </P>
        <P style="text-indent: 0cm;"><img src="http://latex.codecogs.com/gif.latex?\mathbf{-DBLeak}\left( i \right)" />：如果通过这个查询查询了索引<img
                src="http://latex.codecogs.com/gif.latex?i" />，那么它将返回一个失败符号<img
                src="http://latex.codecogs.com/gif.latex?\bot" />。否则，该查询根据数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />的第<img
                src="http://latex.codecogs.com/gif.latex?i" />
            行返回一个类似的Wifi指纹<img
                src="http://latex.codecogs.com/gif.latex?F' \leftarrow \mathrm{FPTSim}\left( i \right)" />。
        </P>
        <P><img
                src="http://latex.codecogs.com/gif.latex?\mathbf{InitCorruptO}" />查询被用于建模针对服务器隐私的指纹获取攻击（在单边恶意设置下），即恶意客户端可能会选择特殊的指纹(例如全0)来试图获取服务器的机密指纹数据库。由此查询初始化的预言机称为（位置）已暴露预言机。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{InitHonestO}" />查询用于初始化诚实的(未暴露)预言机，这是基于一个由敌手根据参考位置索引
            <img src="http://latex.codecogs.com/gif.latex?i" />和半径<img
                src="http://latex.codecogs.com/gif.latex?rds" />指定的区域确定的。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{Execute}_\mathrm{PPIL} " />查询形式化定义了被动攻击敌手行为。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptC}" />和<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptS}" />
            查询分别形式化定义了一个诚实客户端和服务器的长期密钥腐化。被腐化的实体被称为不诚实或恶意的实体。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{RandReveal} " />查询用于模拟针对临时密钥的暴露攻击。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{DBLeak}" />查询模拟了敌手腐化服务器部分参考指纹的攻击能力。<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{LocReveal}" />
            查询用于建模已知位置攻击。<img src="http://latex.codecogs.com/gif.latex?\mathbf{LocTest}" />查询被用于形式化定义敌手攻破客户端隐私的能力。
        </P>
        <p>接下来，我们基于传统的安全游戏方式，针对客户端隐私和服务器隐私两个方面来定义相关安全实验和安全目标。</p>
        <P style="text-indent: 0cm;"><b>客户端隐私实验</b><img
                src="http://latex.codecogs.com/gif.latex?\mathrm{EXP}_{\Pi ,\mathcal{A}}^{\mathrm{CP}}\left( {\kappa ,D} \right)" />：输入安全参数<img
                src="http://latex.codecogs.com/gif.latex?\kappa" />和服务器的数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />，模拟器<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{C}" />和敌手<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />基于目标PPIL方案<img
                src="http://latex.codecogs.com/gif.latex?\Pi" />执行以下游戏步骤：
        </P>
        <p>1. 初始化阶段，模拟器 <img src="http://latex.codecogs.com/gif.latex?\mathcal{C}" />首先实例化一个预言机集合，并为所有λ +
            1个诚实的参与者生成所有公钥/私钥对和所有其它公共信息。<img src="http://latex.codecogs.com/gif.latex?\mathcal{C}" />返回所有公共信息给<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />，包括<img
                src="http://latex.codecogs.com/gif.latex?\left\{ {p{k_\tau }} \right\}_{\tau  = 1}^{\lambda  + 1}" />
            ，<img src="http://latex.codecogs.com/gif.latex?\mathsf{LT}" />，APT以及<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{PD} " />。
        </p>
        <p>2. <img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />可以自适应调用多项式数量的以下查询：<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{InitCorruptO}" />，<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{InitHonestO}" />，<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{InitCorruptO}" />，<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{Execute}_\mathrm{PPIL}" />，
            <img src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptC}" />，<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptS}" />，<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{LocReveal}" />以及<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{RandReveal}" />。并且，在某个时刻，<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />可能会询问一次
            <img
                src="http://latex.codecogs.com/gif.latex?\operatorname{\mathbf{LocTest}}\left(\tau^{*}, s^{*}\right)" />。
        </p>
        <P>3. 在游戏结束时，<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />可能终止并输出一个测试比特b'作为对<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{\mathbf{LocTest}}\left(\tau^{*}, s^{*}\right)" />查询中的比特b的猜测。
        </P>
        <p>4. 同时，如果满足以下条件之一该实验将返回失败符号⊥：（i）<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />没有询问<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{\mathbf{LocTest}}\left(\tau^{*}, s^{*}\right)" />查询；（ii）<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{\mathbf{LocTest}}\left(\tau^{*}, s^{*}\right)" />查询查询返回失败符号⊥；（iii）<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />
            向测试预言机请求 <img
                src="http://latex.codecogs.com/gif.latex?\mathbf{ InitCorruptO }\left(\tau^{*}, s^{*}, F^{*}\right)" />查询；（iv）<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />询问过<img
                src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptC} \left (\tau^{*}\right ) " />查询；（v）<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />请求过 <img
                src="http://latex.codecogs.com/gif.latex?\mathbf { RandReveal }\left(\tau^{*}, s^{*}\right)" />
            查询或 <img src="http://latex.codecogs.com/gif.latex?\mathbf { RandReveal }\left(\lambda+1, t^{*}\right)" />查询,
            其中 <img src="http://latex.codecogs.com/gif.latex?\pi _{\lambda  + 1}^{t^*}" />为测试预言机的伙伴预言机；（vi）向测试预言机请求<img
                src="http://latex.codecogs.com/gif.latex?\mathbf { LocReveal }\left(\tau^{*}, s^{*}\right)" />查询。</p>
        <p>5. 如果b = b'，实验结果将最终返回1，否则返回0。</p>
        <p>如果一个敌手运行了实验之后没有失败，我们称其为“合法的”敌手。一个合法的敌手不应违反上述4步所定义的规则。因为，违反（iii）到（v）中的任意一条规则都会直接导致客户端隐私泄露，而非攻破协议本身。因此，在实验中应该禁止这些情况。
        </p>
        <p style="text-indent: 0cm;"><b>定义3（客户端隐私）。</b>上述实验中合法敌手<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />的优势定义为<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{Adv}_{\Pi, \mathcal{A}}^{\mathrm{CP}}(\kappa, \mathrm{D}):=\left|\operatorname{Pr}\left[\operatorname{EXP}_{\Pi, \mathcal{A}}^{\mathrm{CP}}(\kappa, \mathrm{D})=1\right]-\frac{1}{2}\right|" />
            。如果对于所有<img src="http://latex.codecogs.com/gif.latex?\mathrm{PPT}" />合法敌手<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />和给定<img
                src="http://latex.codecogs.com/gif.latex?\kappa " />和D，敌手优势<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{Adv_{\Pi ,\mathcal{A}}^{CP}\left( {\kappa ,D} \right)}" />是一个可忽略的函数，则
            <img src="http://latex.codecogs.com/gif.latex?\mathrm{PPIL}" />方案满足客户端隐私。
        </p>
        <p  style="text-indent: 0cm;"><b>服务器隐私。</b>总体而言，一个PPIL方案能满足服务器隐私，它必须满足下述条件：所有多项式时间敌手无法生成一个能提供与服务器真实数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />相似的功能的数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D'} " />。我们可以将基于<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D'} " />计算的位置称为伪造位置，将基于<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />
            计算的位置称为真实位置。给定两个数据库<img src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />和<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D'} " />，我们有以下类似的事件（如图1所示）：
            -相似事件（Similar Event，SE）：对于Wifi指纹<img
                src="http://latex.codecogs.com/gif.latex?{F_i} = \left\{ {f_j} \right\}_{j = 1}^N" />的客户端位置查询，相应的位置
            <img src="http://latex.codecogs.com/gif.latex?{L_i}" />和伪造位置<img
                src="http://latex.codecogs.com/gif.latex?{L_i}'" />最多存在距离<img
                src="http://latex.codecogs.com/gif.latex?\rho" />，换句话说，
            <img
                src="http://latex.codecogs.com/gif.latex?\operatorname{Dist}\left(L_{i}, L_{i}^{\prime}\right) \leq \rho" />，其中<img
                src="http://latex.codecogs.com/gif.latex?\rho" />是预先定义的距离差阈值(单位为米)。
        </p>
        <div class="insert_img"> <img class="img" src="../assets/image/img1.png"> 图1. 水平面(左)和垂直面(右)中类似的事件发生示例（小黑点代表<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{LT}" />
            中的参考位置。红点表示真实位置<img src="http://latex.codecogs.com/gif.latex?\quad\mathit{L}" />。绿色圆点表示发生类似事件的伪造位置<img
                src="http://latex.codecogs.com/gif.latex?{L_i}'" />。蓝色圆点表示未发生类似事件的伪造位置
            <img src="http://latex.codecogs.com/gif.latex?{L_i}''" />）。
        </div>
        <p>两个数据库的“相似功能”可以大致说明如下：给定一些不同的客户端的位置查询，SE的发生率大于预先定义的成功阈值<img
                src="http://latex.codecogs.com/gif.latex?\alpha" />(例如<img
                src="http://latex.codecogs.com/gif.latex?\alpha=0.7 " />)，其中
            <img src="http://latex.codecogs.com/gif.latex?TF" />是由<img
                src="http://latex.codecogs.com/gif.latex?\left| {TF} \right| > M" />个随机位置的不同指纹组成的测试集。为了测试两个数据库之间的功能相似度，我们定义了一个相识度检测函数
            <img src="http://latex.codecogs.com/gif.latex?\mathrm{SimilarTest}" />是一个函数：输入两个数据库<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D} " />和<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D'} " />与相关的参考位置列表<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{LT} " />和<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{LT'}" />(分别)和测试集
            <img src="http://latex.codecogs.com/gif.latex?TF" />，其输出0或1测试结果（其中1代表相似）。<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{SimilarTest}" />的执行步骤包括：
        </p>
        <P  style="text-indent: 0cm;">- 初始化SE计数变量<img src="http://latex.codecogs.com/gif.latex?cnt: = 0" />。设指纹<img
                src="http://latex.codecogs.com/gif.latex?F \in TF" />，根据<img
                src="http://latex.codecogs.com/gif.latex?\[{F_i}" />
            , <img src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />和<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{LT}" />计算出的真实位置为<img
                src="http://latex.codecogs.com/gif.latex?L_{i}=\left(x_{i}, y_{i}, z_{i}\right)" />，并且基于
            <img src="http://latex.codecogs.com/gif.latex?{F_i}" />,<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D'}" />和<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{LT'}" />计算伪位置<img
                src="http://latex.codecogs.com/gif.latex?L_{i}^{\prime}=\left(x_{i}^{\prime}, y_{i}^{\prime}, z_{i}^{\prime}\right)" />
            。对于<img src="http://latex.codecogs.com/gif.latex?i \in \left[ {\left| {TF} \right|} \right]" />，如果<img
                src="http://latex.codecogs.com/gif.latex?\text { Dist }\left(L_{i}, L_{i}^{\prime}\right) \leq \rho" />，则<img
                src="http://latex.codecogs.com/gif.latex?cnt = cnt + 1" />。
        </P>
        <P  style="text-indent: 0cm;">- 最后，如果<img src="http://latex.codecogs.com/gif.latex?\frac{cnt}{|TF|}> \alpha " />，则返回1；否则，返回0。</P>
        <p  style="text-indent: 0cm;">安全实验<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{EXP}_{\Pi, \mathcal{A}}^{\mathrm{SP}}(\kappa, \mathrm{D}, \mathrm{LT}, \rho, \alpha, \phi)" />：输入安全参数<img
                src="http://latex.codecogs.com/gif.latex?\kappa" />、服务器的数据库
            <img src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />和距离精度阈值<img
                src="http://latex.codecogs.com/gif.latex?\rho" />上之后，基于PPIL方案Π，安全实验在模拟器
            <img src="http://latex.codecogs.com/gif.latex?\mathcal{C}" />和敌手<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />之间博弈进行，主要执行以下步骤：
        </p>
            <p>1. 模拟器 <img src="http://latex.codecogs.com/gif.latex?\mathcal{C}" />首先实现一个预言机实例集合，并为所有λ +
                1个诚实的参与者和所有其他公共信息生成公钥/私钥对。所有公共信息都给了<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" /></p>
            <p>2. <img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />可以分别发起多项式数量的<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{InitCorruptO}" />、<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{CorruptC}" />
                、<img src="http://latex.codecogs.com/gif.latex?\mathbf{Execute}_\mathrm{PPIL}" />、<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{RandReveal}" />、<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{LocReveal}" />
                查询，以及发起最多<img src="http://latex.codecogs.com/gif.latex?\phi" />次<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{DBLeak}" />查询。</p>
            <p>3. 最终，<img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />返回一个数据库<img
                    src="http://latex.codecogs.com/gif.latex?\mathsf{D'}" />和相关的参考位置列表，即具有M'个参考位置的<img
                    src="http://latex.codecogs.com/gif.latex?\mathsf{LT'}" />。如果
                <img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />请求一个<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{RandReveal}\left( {\lambda  + 1, \cdot } \right)" />查询或者超过<img
                    src="http://latex.codecogs.com/gif.latex?\phi" />次<img
                    src="http://latex.codecogs.com/gif.latex?\mathbf{DBLeak}" />查询，实验将返回一个失败符号⊥。
            </p>
            <p>4. 实验返回<img
                    src="http://latex.codecogs.com/gif.latex?\mathsf{SimilarTest}\left( {\mathrm{D,D',LT,LT',}TF} \right)" />。
            </p>
        <p  style="text-indent: 0cm;"><b>定义4（服务器隐私）。</b>上述实验中合法敌手 <img src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />的优势定义为<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{Adv_{\Pi ,\mathcal{A}}^{SP}\left( {\kappa ,D,LT,\rho ,\alpha ,\phi } \right): = \Pr \left[ {EXP_{\Pi ,\mathcal{A}}^{SP}\left( {\kappa ,\mathrm{D,LT,}\rho ,\alpha ,\phi } \right) = 1} \right]}" />
            。如果对于所有的<img src="http://latex.codecogs.com/gif.latex?\mathrm{PPT}" />合法敌手<img
                src="http://latex.codecogs.com/gif.latex?\mathcal{A}" />，其优势<img
                src="http://latex.codecogs.com/gif.latex?\mathrm{Adv_{\Pi ,\mathcal{A}}^{SP}\left( {\kappa ,{D,LT,}\rho ,\alpha ,\phi } \right)}" />基于κ是一个可忽略的函数，我们就说一个PPIL方案
            <img src="http://latex.codecogs.com/gif.latex?\Pi" />能满足服务器安全。
        </p>
        <p><b>安全模型应用。</b>为了展示项目组提出的新安全模型的有效性，我们基于该模型和博弈可证明方法，证明了项目组负责人在INFOCOM提出的一个基于Paillier加法同态加密和Yao混淆电路的混合型PPIL协议（以下简称为YJ方案）。
            本项目们可以得出以下安全结论：</p>
        <p  style="text-indent: 0cm;"><b>结论1：</b>假设Paillier 公钥加密方案<img src="http://latex.codecogs.com/gif.latex?\mathsf{Pai}" />是安全的，并且安全函数执行方案<img
                src="http://latex.codecogs.com/gif.latex?\mathsf{SF}" />是安全的，则攻击者基于数据库 <img
                src="http://latex.codecogs.com/gif.latex?\mathsf{D}" />攻破<img
                src="http://latex.codecogs.com/gif.latex?YJ" />方案的客户端隐私安全的优势为
            <img
                src="http://latex.codecogs.com/gif.latex?\mathrm{Adv_{YJ, \mathcal{A}}^{CP}\left( {\kappa ,D} \right) \le \left( {d\lambda } \right) \cdot \left( {\left( {N + 1} \right) \cdot Adv_{Pai, \mathcal{B}}^{ind - cpa}\left( \kappa  \right) + \frac{M}{2} \cdot Adv_{SF,\varepsilon ,\Phi }^{\Pr i.ind}\left( {\kappa ,1} \right)} \right)}" />
            并且是可忽略的。
        </p>
        <p  style="text-indent: 0cm;"><b>结论2：</b>假设SF是安全，攻击者攻破YJ方案的服务器安全的优势为<img
                src="http://latex.codecogs.com/gif.latex?\operatorname{Adv}_{\mathrm{YJ}, \mathcal{A}}^{\mathrm{SP}}(\kappa, \mathrm{D}, \mathrm{LT}, \rho, \alpha, \phi) \leq \mathrm{d} \cdot 1 \cdot \operatorname{Adv}_{\mathrm{SF}, \varepsilon, \Phi}^{\mathrm{Pri} . i n d}(\kappa, 2)+\frac{\theta \cdot \mathrm{d} \cdot 1}{2^{\kappa}}+\operatorname{Adv}_{\mathcal{D}}^{\mathrm{DBH}}(\kappa, \mathrm{D}, \mathrm{LT}, \rho, \alpha, \phi)" />，并且是可忽略的。
        </p>
        <p class="min_title"><b>2）可外包的室内定位隐私保护协议设计</b></p>
        <p>在过去的十年里，在室内环境中使用的基于位置的服务 （Location based services, LBS）
            数量不断增长，如商场的定向广告或查找附近的朋友。虽然位置服务隐私保护有一些成熟的解决方案，但对于位置信息获取（即定位）过程的隐私保护还缺乏安全高效的协议。但是，室内定位隐私保护，是所有其他室内LBS隐私保护的前提条件。在研究论文成果<sup>[3]</sup>中，项目负责人和德国达姆斯塔德工业大学的Thomas
            Schneider研究团队、芬兰坦普雷科技大学的研究团队、以及芬兰赫尔辛基大学开展研究合作，基于ABY隐私保护框架共同提出了被一个被称作PILOT的室内定位隐私保护协议（PPIL），该协议是第一个实用高效的基于密码技术的PPIL协议。合作团队基于ABY两方安全计算框架和4种不同的基于Wi-Fi指纹定位方式，分别设计和实现了相应的的
            PPIL 的协议，并进行了严格的性能评估。为降低移动终端设备的计算和网络开销，我们安全地将主要计算过程（即两方安全计算）外包给两个非串谋的半诚实计算服务器。PILOT的主要协议步骤如图2所示。</p>
        <div class="insert_img"> <img class="img" src="../assets/image/img2.png"> <span>图2. PILOT总体框架图</span></div>
        <p  style="text-indent: 0cm;">主要协议步骤如下：</p>
        <p  style="text-indent: 0cm;">1） 客户端从附近N个AP收集信号强度信息RSS 形成Wifi信号指纹<img
                src="http://latex.codecogs.com/gif.latex?(RSS_1, RSS_2, \ldots, RSS_N)" />, 并形成秘密共享列表<img
                src="http://latex.codecogs.com/gif.latex?(<RSS_1>, <RSS_2>, \ldots, <RSS_N>)" />。</p>
        <p  style="text-indent: 0cm;">2） 客户端发送<img
                src="http://latex.codecogs.com/gif.latex?(<RSS_1>_0, <RSS_2>_0, \ldots, <RSS_N>_0)" />给外包服务器T0，发送<img
                src="http://latex.codecogs.com/gif.latex?(<RSS_1>_1, <RSS_2>_1, \ldots, <RSS_N>_1)" />给外包服务器T1。</p>
        <p  style="text-indent: 0cm;">3） 外包服务器T0和T1执行Wifi信号指纹基PPIL的安全两方计算协议STPC，并分别获得结果地址的秘密共享<img
                src="http://latex.codecogs.com/gif.latex?(<x>, <y>, <z>)" />。</p>
        <P  style="text-indent: 0cm;">4） T0和T1分别发送<img src="http://latex.codecogs.com/gif.latex?(<x>_0, <y>_0, <z>_0)" />和<img
                src="http://latex.codecogs.com/gif.latex?(<x>_1, <y>_1, <z>_1)" />给客户端。</P>
        <p  style="text-indent: 0cm;">5） 客户端根据收到的结果地址秘密共享值重构地址<img src="http://latex.codecogs.com/gif.latex?(x,y,z)" /></p>
        <p>我们设计的PPIL协议混合了不同的两方安全计算技术，优化了所需隐私保护相关逻辑电路的尺寸和深度。尤其是我们优化设计了一个具有低电路深度的可用于快速计算k-近邻法来进行高速定位。此外
            ，在不损失定位精度的前提下，我们将Wifi 指纹的比特数从8位降到了4位，这将极大缩小隐私保护电路的尺寸，相关实验结果如图3所示：</p>
        <div class="insert_img"> <img class="img" src="../assets/image/img3.png"> <span>图3. Wifi信号指纹效能实验结果</span></div>
        <p>下表展示了PILOT在有241个访问接入点与伸缩因子<img src="http://latex.codecogs.com/gif.latex?\gamma=16" />比特的情况下的总体性能指标，其中<img
                src="http://latex.codecogs.com/gif.latex?\ell" />为信号强度长度。</p>
        <div class="insert_img"> <span>表1. PILOT总体性能指标</span> <img class="img" src="../assets/image/table1.png"></div>
        <p>我们通过不同实验结果表明，PILOT协议与以前提出的PPIL
            协议相比具有更优的执行效率，例如和Li等人在INFOCOM'14提出的PPIL协议相比PILOT的执行效率要快大约553倍，和Ziegeldorf等人在WiSec'14提出的PPIL协议相比PILOT执行效率要快大约500倍。从具体的性能数据来说，我们在普通商用移动设备端进行的实现和性能评估显示实际定位时间不到1秒，即使对于最精确的定位模式下，PILOT可以每天处理超过
            50 万个 PPIL 查询。</p>
        <p class="min_title"><b>3）高速隐私位置接近性检测协议</b></p>
        <p>位置接近性检测检测是一种常见的基于位置的服务，可用于查找附近的朋友或出租车等具体应用。目前在该类LBS中，如何保护位置隐私信息，是本研究内容需要解决的主要问题。在论文研究成果<sup>[5]</sup>中，合作团队基于不同的两方安全计算技术，包括ABY两方安全计算框架、DGK加法同态加密和Elgamal加法同态加密，设计和实现了多种置隐私接近性检测检测协议（Privacy-preserving
            location proximity, PPLP），在实现位置接近性检测服务的同时保护通信双方的位置信息。项目组根据不同的应用场景以及相应的优化需求，设计了不同的PPLP 协议，这些应用场景包括：
            1）有限的网络带宽；2）有限的电量和计算能力；3）运行时间敏感。</p>
        <p>以下我们以基于圆形范围的PPLP协议为例展开介绍，对于多边形不规则的接近性范围检查见论文。这里圆形的接近性范围检查，主要采用修改后（即平方距离）欧几里得距离的扩展方程：<img
                src="http://latex.codecogs.com/gif.latex?d=x_0^2+x_1^2-2 x_0 x_1+y_0^2+y_1^2-2 y_0 y_1" />，其中<img
                src="http://latex.codecogs.com/gif.latex?x_i" />和<img
                src="http://latex.codecogs.com/gif.latex?y_i" />为用户<img
                src="http://latex.codecogs.com/gif.latex?i \in \{0,1\}" />的坐标值，或用较大坐标中减去较小坐标，
            即<img
                src="http://latex.codecogs.com/gif.latex?\left(x_{\max }-x_{\min }\right)^2+\left(y_{\max }-y_{\min }\right)^2" />。
        </p>
        <P style="text-indent: 0cm;"><b>基于Yao和算术共享的PPLP方案<img
                    src="http://latex.codecogs.com/gif.latex?A B Y_{A Y}" />。</b>本项目设计了一个混合使用算术共享和Yao共享的PPLP协议<img
                src="http://latex.codecogs.com/gif.latex?A B Y_{A Y}" />。相比于简单使用Yao共享方案，混合使用算术共享后可以带来以下性能提升：(i)
            减少PPLP的计算和通信开销；(ii) 降低PPLP协议在低延迟网络环境下的协议运行时间。<img
                src="http://latex.codecogs.com/gif.latex?A B Y_{A Y}" />的构造总共含有以下的门电路：
            <img src="http://latex.codecogs.com/gif.latex?6MUL_A(\sigma)+A2Y(\sigma)+GT(\sigma)" />，其中<img
                src="http://latex.codecogs.com/gif.latex?MUL_A(\ell)" />为在算术共享中
            <img src="http://latex.codecogs.com/gif.latex?\sigma" />比特的乘法门，<img
                src="http://latex.codecogs.com/gif.latex?A2Y(\sigma)" />为<img
                src="http://latex.codecogs.com/gif.latex?\sigma" />比特的算术共享和Yao共享之间转换操作。<img
                src="http://latex.codecogs.com/gif.latex?A B Y_{A Y} " />协议主要步骤如图4所示。
        </P>
        <div class="insert_img"> <img class="img" src="../assets/image/img4.png"> <span>图4. <img
                    src="http://latex.codecogs.com/gif.latex?A B Y_{A Y} " />协议步骤</span></div>
        <p style="text-indent: 0cm;"><b>基于ELGamal的PPLP方案<img
                    src="http://latex.codecogs.com/gif.latex?{\textstyle \sum_{ELG}}" />。</b>基于加法同态的ELGamal公钥加密算法和布隆过滤器（Bloom
            Filter, BF），本项目提出了一个PPLP协议<img
                src="http://latex.codecogs.com/gif.latex? {\textstyle \sum_{ELG}} " />。该方案的主要构造思路基于以下重要观察发现：对于只需要知道位置接近结果的用户A，在线预处理（包括参数准备等）阶段并不需要进行完全的解密。因此，本项目用基于ECC基ELGamal可以通过定制预处理方法，提前进行部分开销大的运算，从而可以获得更好的在线通信复杂性。
            <img src="http://latex.codecogs.com/gif.latex? {\textstyle \sum_{ELG}} " />协议的具体运行步骤如图5所示。
        </p>
        <div class="insert_img"> <img class="img" src="../assets/image/img5.png"> <span>图5. <img
                    src="http://latex.codecogs.com/gif.latex?{\textstyle \sum_{ELG}}" />协议步骤</span></div>
        <P>本项目设计的PPLP协议运行时间仅需要几毫秒到几百毫秒之间，并且数据通信开销需要数百字节到一兆字节之间。此外，对于PPLP中计算量大的计算过程可以通过离线的预计算过程来提前完成，这使得在线的位置接近性检测运算过程只需几毫秒运行。实验运行结果如图6所示。
        </P>
        <div class="insert_img"> <img class="img" src="../assets/image/img6.png"> <span>图6. PPLP实验结果</span></div>
        <p class="min_title"><b>4）室内隐私位置服务异常行为保护技术</b></p>
        <p>为了保证存在恶意攻击者情况下的室内位置隐私与安全，本项目从消息认证原语、位置证明、透明与隐私数据验证、以及安全通信等方面开展全面研究。部分核心工作介绍如下:</p>
        <p><b>轻量级连续消息认证。</b></p>
        <p>在一些常见位置服务应用中（如定位和导航等），位置服务模块（位置服务器或无线接入点）和用户设备之间往往需要连续不断发送实时位置相关信息。如何快速认证这些信息是本年度研究过程中解决的一个主要问题之一。项目组针对这种连续认证的需求，设计了一种全新的轻量级数字签名算法，相关成果<sup>[14]</sup>
            发表在国际著名安全会议AsiaCCS 2020。</p>
        <p> LiS主要利用变色龙散列函数能够通过秘密后门针对已有消息计算冲突的能力，来实现数字签名功能。注意，给定变色龙哈希函数CHF和对应私钥<i>sk</i>，
            CHF包含一个冲突计算函数<img src="http://latex.codecogs.com/gif.latex?Coll" />
            能够基于一个消息和随机数对<img src="http://latex.codecogs.com/gif.latex?\left(m^{\prime},r^{\prime}\right)" />
            和任意其它消息<i>m</i>，使用私钥<i>sk</i>有效计算碰撞值<i>r</i>满足<img
                src="http://latex.codecogs.com/gif.latex?CHF(m,r)=CHF\left(m^{\prime},r^{\prime}\right)" />。
            LiS运用该冲突计算特点，设置一个可信服务器不断输入伪消息和随机数对<img
                src="http://latex.codecogs.com/gif.latex?\left(m_{i}^{\prime},r_{i}^{\prime}\right)" />，然后计算对应变色龙哈希值作为验证密钥<i>vk</i>的一部分，其中
            <img src="http://latex.codecogs.com/gif.latex?{1\leq i\leq l}" />，<img
                src="http://latex.codecogs.com/gif.latex?l" />
            表示能够被<i>vk</i>验证的签名数。签名者在对实际真实消息<img src="http://latex.codecogs.com/gif.latex?m_{i}" />签名时，只需要基于随机数<img
                src="http://latex.codecogs.com/gif.latex?r_{i}^{\prime}" />
            和其私钥<i>sk</i>计算消息<img src="http://latex.codecogs.com/gif.latex?m_{i}" />的碰撞值<img
                src="http://latex.codecogs.com/gif.latex?r_{i}" />。
            为了进一步优化性能，让签名方案能够部署在资源受限的签名设备上，我们将所有伪消息<img
                src="http://latex.codecogs.com/gif.latex?m_{i}" />假定为M，并利用通用哈希函数UHF将所有伪随机数哈希链接起来，即
            <img
                src="http://latex.codecogs.com/gif.latex?r_{i}^{\prime}=U H F\left(k, r_{i-1}^{\prime}\right)" />，其中<img
                src="http://latex.codecogs.com/gif.latex?{1\leq i\leq l}" />、
            <img src="http://latex.codecogs.com/gif.latex?r_{0}" />随机生成，<i>k</i>是UHF的随机密钥。签名者只需要存储几百比特数据<img
                src="http://latex.codecogs.com/gif.latex?\left(s k, s k \cdot M, r_{0}^{\prime}, k\right)" />
            用于消息签名。核心算法思路如图7所示。
        </p>
        <div class="insert_img"> <img class="img" src="../assets/image/img7.png"> <span>图7. LiS总体框架</span></div>
        <p> 相比于以往的类似轻量级数字签名算法（如表2所示），LiS在实现高效数字签名过程，同时能够将公钥的更新外包给可信第三方并且更新过程不会影响签名方。
            在安全方面，LiS能够规约到标准的离散对数问题，而非以往轻量级算法安全分析中较强的或非标准安全假设（如Forking Lemma）。
            因此，LiS非常适合于需要连续认证的嵌入式位置应用环境中（如室内定位）。</p>
        <div class="insert_img"> <span>表2. LiS实验结果</span><img class="img" src="../assets/image/table2.png"> </div>
        <p><b>群时间基一次性口令算法及其在隐私位置证明协议构建中的应用。</b></p>
        <P> 项目负责人以第一作者在国际计算机安全应用会议ACSAC 2021发表了项目最新研究成果<sup>[12]</sup>。
            该成果是对传统时间基一次性口令（Time-based One-Time Password，TOTP）的进一步发展和创新，首次引入了身份隐私安全属性。
            为解决证明者的身份隐私保护问题，我们的方案是将TOTP扩展到组场景，来同时实现组成员身份认证和隐私保护。
            为此，项目负责人等提出了一种全新的身份认证方案——群时间基一次性口令（Group Time-based One-time Passwords，GTOTP），
            它允许证明者证明是经过身份认证的组成员，而不会暴露其真实身份。在具体方案构建方面，项目负责人提出了一种新的结构，
            结合梅克尔树（Merkle Tree）和布隆过滤器（Bloom Filter）能够将任何非对称TOTP方案转换为GTOTP方案，
            并能使得组验证状态（Group Verification State, GVST）的大小为常数级别。图8展示了一个验证窗口大小为2、用户数量为4的GTOTP算法实例，其中<img
                src="http://latex.codecogs.com/gif.latex?\hat{vp}^j_{\text{ID}}" />
            为用户ID在第j个验证周期的TOTP验证点。</P>
        <div class="insert_img"> <img class="img" src="../assets/image/img8.png"> <span>图8. GTOTP算法示例</span></div>
        <p> 此外，项目负责人运用GTOTP来构建了一个高效的具有隐私保护功能的位置证明者案（Privacy-preserving Proof of Location，PPPoL）。
            在PPPOL中，证明者在一些附近的目击者（Witness）的帮助下生成防篡改的位置证明，使验证者确信它在给定的时间处于特定的位置，
            并能够保护证明者和目击者的身份和位置隐私不泄露给对方。PPPoL在现实世界中有许多应用，如跟踪供应链中的货物流动，
            和追踪流行病的密切接触者等。我们构建的PPPoL结合了GTOTP、承诺协议（Commitment Protocol）和隐私位置接近性检测（Privacy-preserving Location Proximity,
            PPLP）
            方案。具体执行过程中，证明者首先将其GTOTP密码和基于其位置生成的PPLP请求广播给附近的潜在目击者。如果附近的用户想要成为目击者参与协议，
            它们会回复自己的GTOTP密码，并基于自己的位置生成PPLP响应（证明其和证明者接近），同时基于相应的GTOTP密码的秘密种子对相关PPLP响应消息进行承诺——作为证明片段。
            由于位置证明不需要立即验证，我们使用相应的GTOTP密码的秘密种子作为生成承诺的密钥是有效的且切实可行的。
            也就是说，我们利用GTOTP作为工具，将PoL方案中的其他成分串联在一起。在验证过程中，对证明做出贡献的证明者及相应证人，
            在GTOTP密码过期后，目击者可以公开自己的秘密种子，帮助证明者和验证者进行承诺验证。研究团队在树莓派(Raspberry Pi)上评测了所设计的方案，
            通过实验数据，验证了基于GTOTP构建的PPPoL的实用性。其中，密码生成和验证时间分别是微秒级和毫秒级的，而位置证明生成时间小于1秒。
            表3展示了本项目提出的PPPOL方案与现有相关协议的比较结果，可以看出我们的方案在提供最大多数必要安全属性，同时具有轻量级的特性。</p>
        <div class="insert_img"><span>表3. 分布式位置证明协议比较</span> <img class="img" src="../assets/image/table3.png"> </div>
        <P>主要创新点总结如下：（i）首次定义了GTOTP密码学概念和相关安全模型——同时涵盖身份可追溯性和匿名性;（ii）设计了一个高效和通用的GTOTP方案，
            其能将一个非对称的TOTP转换为一个GTOTP;（iii）为了展示GTOTP的应用，我们使用GTOTP构建了一个高效的且具有隐私保护功能的PPPoL方案;（iv）开发原型系统，
            并评测了GTOTP和PoL方案在树莓派上的性能表现。</P>
        <p>
            其它安全通信研究。
        </p>
        <p><b>轻量级代理认证中的身份欺诈检测。</b>项目组在信息物理融合系统安全旗舰国际研讨会CPSS 2021上，发表关于轻量级委派身份认证和身份欺诈检测的相关论文成果。
            该工作可以用于保护各类室内隐私位置服务相关协议（包括定位和基于位置服务）的通信安全，尤其考虑更多类型的恶意攻击威胁（包括内部腐化导致的安全威胁）。</p>
        <p>
            为了减轻委托身份认证框架中腐化身份认证服务器的危害，项目负责人等在本工作中专注于研究身份欺诈检测 (Identity Fraud Detection,
            IFD)。本项目的目标是设计轻量级相互身份认证协议，使诚实的身份认证服务器不仅可以检测被腐化的客户端，还可以检测由其它被感染的身份认证服务器引起的身份欺诈事件。本方案采用了类似的跨域身份认证标准化系统模型——即每个客户端在每次认证协议执行过程中会从身份认证服务器池中选取一个与之有联系身份认证委托服务器。本方案实现
            IFD 的主要思路是利用不断前向进化的一次性签名 (One-time Signature, OTS) 密钥作为认证因子，每一个客户端使用其当前的 OTS
            密钥在签名中提交其下一个次将使用的OTS公钥，从而形成动态OTS签名链。客户端选择的认证代理服务器负责更新其OTS密钥。对于大小为z的认证服务器池，会话中的每个 OTS 密钥更新过程都将被后续会话中的
            z-1个身份认证服务器进行分别检查，设计的协议能够保证当前发生的IFD会被这z-1个身份认证服务器中的至少一个诚实的服务器识别。
        </p>
        <p>
            为了抵抗网络故障，客户端应该在本地“缓存”当前未检查的签名链并在下一个会话中“重播”它们。当认证服务器完成其委托的认证任务时，它应该诚实地将 OTS
            公钥进化过程存入到公共数据库中，以确保所有会话都应该是可审计的。因此，每个z-1 认证服务器都应该通过比较客户端的 OTS 缓存和数据库中记录的历史协议副本来检测客户端的 OTS
            密钥更新过程，以找出身份欺诈攻击。但是，仅使用 OTS 进行身份认证容易受到网络攻击，这是因为攻击者可以干扰 OTS
            公钥更新过程并使得客户端缓存的签名链因过长而失效。例如，攻击者可以在客户端不断发起会话以生成和缓存许多新的 OTS
            公钥（在签名链中），而丢弃身份认证服务器的最后一个确认消息，从而导致服务器单方面进行OTS密钥更新。为了防止网络攻击，我们采用了一种混合方法，即通过使用基于长期对称密钥的消息认证代码和基于非对称密钥的一次性签名（OTS）来实现双向认证协议。长期基于密钥的身份认证在
            IFD 中很有效， 因为如果基于长期密钥的消息验证码在当前会话中有效，但相关的OTS签名无效，那么这意味着诚实方之前肯定已经被冒充过。其次，OTS的安全性可以确保以下两个安全属性：（i） 前向安全性，保护受损的
            OTS 密钥不会影响使用未腐化密钥的历史会话的安全性； （ii） 后向安全性，这意味着如果一方在攻击者将其用于攻击之前运行双向认证以更新已泄露的 OTS 密钥，那么已泄露的密钥将失效（由于其一次性特性）。
        </p>
        <p>
            为了在实际场景中评测本方案的性能，项目团队实现了一个两方系统。系统中的服务器是一台笔记本电脑，而基于树莓派用作 CPS
            设备。实验结果表明（如图9所示），本方案提出的认证协议的通信、计算和内存利用率等性能指标方面均是非常高效的。
        </p>
        <div class="insert_img"> <img class="img" src="../assets/image/img9.png"><img class="img"
                src="../assets/image/img9_1.png"> <span>图9. 具有身份欺诈检测功能的代理认证方案实验结果</span></div>
        <p>
            <b>轻量级满足完美前向安全的认证密钥交换协议。</b> 此外，在认证密钥交换协议研究方面，项目组提出了一个新型的满足完美前向安全（Perfect Forward Secrecy,
            PFS）安全属性的并可以用于物联网环境下的轻量级的认证密钥交换协议（Authenticated Key Exchange, AKE）协议。该协议采用了一种全新的采用于一种新型的动态一次性身份认证凭据（Dynamic
            authentication credential, DAC），每一个凭据仅被用来建立一个会话密钥，从而能够实现 PFS
            的安全目标。新协议可以非常高效地在传感器上实现，因为我们的协议只需要执行哈希函数和异或操作。总的来说，该协议的主要创新性包括：i）提出了一种新型动态一次性身份认证凭据；ii）该新协议并不使用任何公钥密码原语；iii）执行效率比以往满足PFS的AKE协议更高。由于该协议不基于公钥密码学来实现AKE的PFS安全属性，构建的协议执行效率高，开销小，对硬件的计算能力要求更低，从而能在更广泛的嵌入式设备上运行。部署在位置服务安全通信应用中，能大大降低物联网的部署成本。相关成果[18]发表在工业信息旗舰期刊IEEE
            Transactions on Industrial Informatics上。
        </p>
        <h1>三、主要研究成果</h1>
        <p>本项目最终已发表（已录用）22篇与本项目研究内容相关（标注本项目基金号）的学术论文成果，已授权国家发明专利2项。本项目相关论文成果大多发表在国内外高水平著名期刊或者国际会议上，其中包括中国计算机学会（CCF）或者中国密码学会（CACR）推荐A/B类论文8篇
            （如<b>《ACM Transactions on Sensor Networks》、《The Computer Journal》，《IET Information Security》、AsiaCCS 2020, ACSAC
            2021，ACSAC 2022, FSE 2022，和Usenix Security 2023），中科院1区SCI论文2篇（即《IEEE Transactions on Industrial
            Informatics》、《IEEE Internet of Things》）,</b> 以及4篇CACR-C或CCF-C著名会议论文（即<b>IEEE Euro S&P 2019, SACMAT 2022， ProveSec
            2018和CANS 2018）。</b> </p>


























