<template>
    <div>
        <el-container>
            <el-aside min-width:520px>
                <common-aside />
            </el-aside>
            <el-container class="wit">
                <el-header><common-header /></el-header>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import CommonAside from '../components/CommonAside.vue';
import CommonHeader from '../components/CommonHeader.vue';
export default {
    data() {
        return {}
    },
    components:{
        CommonAside,
        CommonHeader 
    }
}
</script>
<style scoped>
.el-header{
    padding:0;
}
.wit{
    display: block;
}
.el-row{
    margin-top:40px;
    display: block;
}

</style>


