<template>
    <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
        :collapse="isCollapse" background-color="#fff" text-color="#666" active-text-color="#ffd04b">
        <!-- <h3>{{ isCollapse ? '成果' : '成果展示网站' }}</h3>
        <el-submenu v-for="item in hasChildren" :key="item.label" :index="item.label">
            <template slot="title">
                <i :class="`el-icon-${item.icon}`"></i>
                <span slot="title">{{ item.label }}</span>
            </template>
            <el-menu-item-group v-for="subItem in item.children" :key="subItem.path">
                <el-menu-item @click="clickMenu(subItem)" :index="subItem.name">
                    <span slot="title">{{ subItem.label }}</span>
                </el-menu-item>
            </el-menu-item-group>
        </el-submenu> -->
        <el-menu-item @click="clickMenu(item)" v-for="item in noChildren" :key="item.name" :index="item.label">
            <!-- <i :class="`el-icon-${item.icon}`"></i> -->
            <span slot="title" class="_title">{{ item.label }}</span>
        </el-menu-item>
    </el-menu>
</template>

<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
    min-width: 400px;
    min-height: 100vh;
    display: fixed;
}

.el-menu--collapse {
    min-height: 100vh;
}

.el-menu {
    border: none;
}

.el-menu-item-group {
    margin-left: 15px;
}
.el-menu-item{
    margin-top:60px;
    background:url(../assets/image/navb.png) no-repeat left center;
    height: 52px;
    margin-left: 100px;
    
}
._title{
    color: #fff;
    position: absolute;
    font-size: 18px;
    line-height: 40px;
}

</style>
  
<script>
export default {
    data() {
        return {
            //isCollapse: false,
            menuData: [
                {
                    path: "/",
                    name: "page5",
                    label: "科研动态",
                    icon: "document",
                    url: "PageFive/PageFive"
                }
            ]
        };
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        //点击菜单
        clickMenu(item) {
            if (this.$route.path !== item.path && !(this.$route.path === '/page1' && (item.path == '/'))) {
                this.$router.push(item.path)
            }
        },
    },

    computed: {
        //没有子菜单
        noChildren() {
            return this.menuData.filter(item => !item.children)
        },
        //有子菜单
        hasChildren() {
            return this.menuData.filter(item => item.children)
        },
        isCollapse() {
            return this.$store.state.tab.isCollapse
        }
    }
}
</script>


