import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '../views/Home.vue'
//import User from '../views/User.vue'
import Main from '../views/Main.vue'
import PageOne from '../views/PageOne.vue'
// import PageTwo from '../views/PageTwo.vue'
// import PageThree from '../views/PageThree.vue'
// import PageFour from '../views/PageFour.vue'
// import PageFive from '../views/PageFive.vue'
// import PageSix from '../views/PageSix.vue'
// import PageSeven from '../views/PageSeven.vue'
Vue.use(VueRouter)


const routes = [
  //主路由
  {
    path:'/',
    component: Main,
    redirect: 'page1',
    children: [  
      //子路由
    //  { path: 'home', component: Home },
     // { path: 'user', component: User },
      { path: 'page1', component: PageOne },
      // { path: 'page2', component: PageTwo },
      // { path: 'page3', component: PageThree },
      // { path: 'page4', component: PageFour },
      // { path: 'page5', component: PageFive },
      // { path: 'page6', component: PageSix },
      // { path: 'page7', component: PageSeven }
    ]
  }
]

const router = new VueRouter({
  routes // (缩写) 相当于 routes: routes
})

export default router


