位置证明者案（Privacy-preserving Proof of Location，PPPoL）。\n        在PPPOL中，证明者在一些附近的目击者（Witness）的帮助下生成防篡改的位置证明，使验证者确信它在给定的时间处于特定的位置，\n        并能够保护证明者和目击者的身份和位置隐私不泄露给对方。PPPoL在现实世界中有许多应用，如跟踪供应链中的货物流动，\n        和追踪流行病的密切接触者等。我们构建的PPPoL结合了GTOTP、承诺协议（Commitment Protocol）和隐私位置接近性检测（Privacy-preserving Location Proximity,\n        PPLP）\n        方案。具体执行过程中，证明者首先将其GTOTP密码和基于其位置生成的PPLP请求广播给附近的潜在目击者。如果附近的用户想要成为目击者参与协议，\n        它们会回复自己的GTOTP密码，并基于自己的位置生成PPLP响应（证明其和证明者接近），同时基于相应的GTOTP密码的秘密种子对相关PPLP响应消息进行承诺——作为证明片段。\n        由于位置证明不需要立即验证，我们使用相应的GTOTP密码的秘密种子作为生成承诺的密钥是有效的且切实可行的。\n        也就是说，我们利用GTOTP作为工具，将PoL方案中的其他成分串联在一起。在验证过程中，对证明做出贡献的证明者及相应证人，\n        在GTOTP密码过期后，目击者可以公开自己的秘密种子，帮助证明者和验证者进行承诺验证。研究团队在树莓派(Raspberry Pi)上评测了所设计的方案，\n        通过实验数据，验证了基于GTOTP构建的PPPoL的实用性。其中，密码生成和验证时间分别是微秒级和毫秒级的，而位置证明生成时间小于1秒。\n        表3展示了本项目提出的PPPOL方案与现有相关协议的比较结果，可以看出我们的方案在提供最大多数必要安全属性，同时具有轻量级的特性。")]),_vm._v(" "),_vm._m(61),_vm._v(" "),_c('P',[_vm._v("主要创新点总结如下：（i）首次定义了GTOTP密码学概念和相关安全模型——同时涵盖身份可追溯性和匿名性;（ii）设计了一个高效和通用的GTOTP方案，\n        其能将一个非对称的TOTP转换为一个GTOTP;（iii）为了展示GTOTP的应用，我们使用GTOTP构建了一个高效的且具有隐私保护功能的PPPoL方案;（iv）开发原型系统，\n        并评测了GTOTP和PoL方案在树莓派上的性能表现。")]),_vm._v(" "),_c('p',[_vm._v("\n        其它安全通信研究。\n    ")]),_vm._v(" "),_vm._m(62),_vm._v(" "),_c('p',[_vm._v("\n        为了减轻委托身份认证框架中腐化身份认证服务器的危害，项目负责人等在本工作中专注于研究身份欺诈检测 (Identity Fraud Detection,\n        IFD)。本项目的目标是设计轻量级相互身份认证协议，使诚实的身份认证服务器不仅可以检测被腐化的客户端，还可以检测由其它被感染的身份认证服务器引起的身份欺诈事件。本方案采用了类似的跨域身份认证标准化系统模型——即每个客户端在每次认证协议执行过程中会从身份认证服务器池中选取一个与之有联系身份认证委托服务器。本方案实现\n        IFD 的主要思路是利用不断前向进化的一次性签名 (One-time Signature, OTS) 密钥作为认证因子，每一个客户端使用其当前的 OTS\n        密钥在签名中提交其下一个次将使用的OTS公钥，从而形成动态OTS签名链。客户端选择的认证代理服务器负责更新其OTS密钥。对于大小为z的认证服务器池，会话中的每个 OTS 密钥更新过程都将被后续会话中的\n        z-1个身份认证服务器进行分别检查，设计的协议能够保证当前发生的IFD会被这z-1个身份认证服务器中的至少一个诚实的服务器识别。\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        为了抵抗网络故障，客户端应该在本地“缓存”当前未检查的签名链并在下一个会话中“重播”它们。当认证服务器完成其委托的认证任务时，它应该诚实地将 OTS\n        公钥进化过程存入到公共数据库中，以确保所有会话都应该是可审计的。因此，每个z-1 认证服务器都应该通过比较客户端的 OTS 缓存和数据库中记录的历史协议副本来检测客户端的 OTS\n        密钥更新过程，以找出身份欺诈攻击。但是，仅使用 OTS 进行身份认证容易受到网络攻击，这是因为攻击者可以干扰 OTS\n        公钥更新过程并使得客户端缓存的签名链因过长而失效。例如，攻击者可以在客户端不断发起会话以生成和缓存许多新的 OTS\n        公钥（在签名链中），而丢弃身份认证服务器的最后一个确认消息，从而导致服务器单方面进行OTS密钥更新。为了防止网络攻击，我们采用了一种混合方法，即通过使用基于长期对称密钥的消息认证代码和基于非对称密钥的一次性签名（OTS）来实现双向认证协议。长期基于密钥的身份认证在\n        IFD 中很有效， 因为如果基于长期密钥的消息验证码在当前会话中有效，但相关的OTS签名无效，那么这意味着诚实方之前肯定已经被冒充过。其次，OTS的安全性可以确保以下两个安全属性：（i） 前向安全性，保护受损的\n        OTS 密钥不会影响使用未腐化密钥的历史会话的安全性； （ii） 后向安全性，这意味着如果一方在攻击者将其用于攻击之前运行双向认证以更新已泄露的 OTS 密钥，那么已泄露的密钥将失效（由于其一次性特性）。\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        为了在实际场景中评测本方案的性能，项目团队实现了一个两方系统。系统中的服务器是一台笔记本电脑，而基于树莓派用作 CPS\n        设备。实验结果表明（如图9所示），本方案提出的认证协议的通信、计算和内存利用率等性能指标方面均是非常高效的。\n    ")]),_vm._v(" "),_vm._m(63),_vm._v(" "),_vm._m(64),_vm._v(" "),_c('h1',[_vm._v("三、主要研究成果")]),_vm._v(" "),_vm._m(65)],1)}
